:root {
  --bg: #f5f8ff;
  --panel: rgba(255, 255, 255, 0.92);
  --panel-2: rgba(255, 255, 255, 0.84);
  --surface: rgba(255, 255, 255, 0.78);
  --border: rgba(var(--accent-rgb), 0.28);
  --text: rgba(11, 18, 32, 0.92);
  --muted: rgba(11, 18, 32, 0.70);
  --muted-2: rgba(11, 18, 32, 0.56);
  --accent-rgb: 9, 179, 115;
  --accent: rgb(var(--accent-rgb));
  --accent-2: rgba(var(--accent-rgb), 0.12);
}

.App {
  min-height: 100vh;
  background: radial-gradient(1200px 520px at 60% -10%, rgba(var(--accent-rgb), 0.10), transparent 55%),
    radial-gradient(900px 460px at 10% 10%, rgba(var(--accent-rgb), 0.08), transparent 55%),
    var(--bg);
  color: var(--text);
}

.Container {
  width: min(980px, calc(100% - 40px));
  margin: 0 auto;
}

.SkipLink {
  position: absolute;
  left: -9999px;
  top: 10px;
  background-color: var(--accent);
  color: rgba(11, 18, 32, 0.92);
  padding: 10px 12px;
  border-radius: 12px;
  font-weight: 700;
}

.SkipLink:focus {
  left: 20px;
}


.Layout {
  display: grid;
  grid-template-columns: 320px 1fr;
  gap: 18px;
  width: min(1200px, calc(100% - 40px));
  margin: 0 auto;
  padding: 20px 0;
}

@media (max-width: 920px) {
  .Layout {
    grid-template-columns: 1fr;
  }
}

.Sidebar {
  position: sticky;
  top: 20px;
  align-self: start;
}

@media (max-width: 920px) {
  .Sidebar {
    position: static;
  }
}

.SidebarCard {
  background: linear-gradient(180deg, var(--panel), var(--panel-2));
  border: 1px solid var(--border);
  border-radius: 18px;
  padding: 16px;
}

.ProfileRow {
  display: flex;
  align-items: center;
  gap: 12px;
}

.Avatar {
  width: 44px;
  height: 44px;
  border-radius: 12px;
  display: grid;
  place-items: center;
  background: radial-gradient(120px 60px at 30% 10%, rgba(var(--accent-rgb), 0.18), transparent 55%),
    rgba(255, 255, 255, 0.70);
  border: 1px solid rgba(11, 18, 32, 0.10);
  font-weight: 900;
  letter-spacing: 0.6px;
}

.ProfileName {
  font-weight: 900;
  letter-spacing: 0.2px;
}

.ProfileRole {
  color: var(--muted);
  margin-top: 2px;
  font-weight: 700;
  font-size: 13px;
}

.Divider {
  height: 1px;
  background: var(--border);
  margin: 14px 0;
}

.KeyValues {
  margin: 0;
  display: grid;
  gap: 10px;
}

.KeyValue {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  gap: 12px;
}

.KeyValue dt {
  color: var(--muted-2);
  font-weight: 800;
  font-size: 13px;
  letter-spacing: 0.2px;
}

.KeyValue dd {
  margin: 0;
  color: var(--text);
  font-weight: 800;
  font-size: 14px;
}

.BlockTitle {
  font-weight: 900;
  font-size: 13px;
  letter-spacing: 0.24px;
  color: var(--muted);
  text-transform: uppercase;
  margin-bottom: 10px;
}

.MiniGrid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}

.MiniStat {
  border: 1px solid var(--border);
  border-radius: 14px;
  padding: 10px;
  background-color: var(--surface);
}

.MiniStatValue {
  font-weight: 950;
  color: var(--accent);
  font-size: 18px;
}

.MiniStatLabel {
  margin-top: 2px;
  color: var(--muted);
  font-weight: 800;
  font-size: 13px;
}

.Bars {
  display: grid;
  gap: 12px;
}

.BarTop {
  display: flex;
  justify-content: space-between;
  font-weight: 800;
  font-size: 14px;
  line-height: 1.3;
  color: var(--text);
}

.Muted {
  color: var(--muted-2);
}

.BarTrack {
  margin-top: 8px;
  height: 10px;
  border-radius: 999px;
  border: 1px solid var(--border);
  background: rgba(var(--accent-rgb), 0.06);
  overflow: hidden;
}

.BarFill {
  height: 100%;
  border-radius: 999px;
  background: linear-gradient(90deg, rgba(var(--accent-rgb), 0.70), rgba(var(--accent-rgb), 1));
}


.Content {
  min-width: 0;
}

.Topbar {
  border: 1px solid var(--border);
  border-radius: 18px;
  background-color: var(--surface);
  padding: 12px 14px;
}

.Nav {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: flex-end;
}

.NavLink {
  color: rgba(11, 18, 32, 0.88);
  text-decoration: none;
  padding: 8px 10px;
  border-radius: 999px;
  border: 1px solid transparent;
  font-weight: 800;
}

.NavLink:hover,
.NavLink:focus-visible {
  border-color: rgba(var(--accent-rgb), 0.40);
  background-color: rgba(var(--accent-rgb), 0.08);
  outline: none;
}

.HeroCard {
  margin-top: 14px;
  border: 1px solid var(--border);
  border-radius: 18px;
  background: radial-gradient(900px 260px at 40% 0%, rgba(var(--accent-rgb), 0.12), transparent 60%),
    var(--surface);
  padding: 18px;
  display: grid;
  grid-template-columns: 1fr 180px;
  gap: 12px;
  align-items: center;
}

@media (max-width: 920px) {
  .HeroCard {
    grid-template-columns: 1fr;
  }
}

.HeroTitle {
  font-size: 28px;
  font-weight: 950;
  letter-spacing: 0.2px;
}

.HeroSubtitle {
  margin-top: 8px;
  color: var(--muted);
  line-height: 1.7;
  max-width: 70ch;
}

.Accent {
  color: var(--accent);
  font-weight: 900;
}

.HeroRight {
  display: grid;
  place-items: center;
}

.HeroBadge {
  width: 120px;
  height: 120px;
  border-radius: 24px;
  display: grid;
  place-items: center;
  font-weight: 950;
  font-size: 34px;
  letter-spacing: 0.8px;
  background: radial-gradient(140px 80px at 20% 10%, rgba(var(--accent-rgb), 0.22), transparent 55%),
    rgba(255, 255, 255, 0.72);
  border: 1px solid rgba(11, 18, 32, 0.10);
}

.Stats {
  margin-top: 12px;
  border: 1px solid var(--border);
  border-radius: 18px;
  background-color: var(--surface);
  padding: 12px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
}

@media (max-width: 920px) {
  .Stats {
    grid-template-columns: repeat(2, 1fr);
  }
}

.Stat {
  border: 1px solid var(--border);
  border-radius: 14px;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.72);
}

.StatValue {
  color: var(--accent);
  font-weight: 950;
}

.StatLabel {
  color: var(--muted);
  font-weight: 800;
  font-size: 12px;
  margin-top: 2px;
}

.Section {
  padding: 18px 0;
}

.SectionTitle {
  margin: 0 0 12px;
  font-size: 20px;
  letter-spacing: 0.2px;
}

.Paragraph {
  margin: 0;
  line-height: 1.75;
  max-width: 80ch;
  color: rgba(11, 18, 32, 0.78);
}

.Actions {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 18px;
}

.ActionsSidebar {
  margin-top: 0;
}

.ActionsCompact {
  margin-top: 12px;
}

.Button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 10px 12px;
  border-radius: 12px;
  border: 1px solid rgba(11, 18, 32, 0.14);
  color: var(--text);
  text-decoration: none;
  font-weight: 700;
}

.ButtonIcon {
  width: 18px;
  height: 18px;
  margin-right: 0;
  flex: 0 0 auto;
  display: block;
}

.Button:hover,
.Button:focus-visible {
  outline: none;
  background-color: rgba(var(--accent-rgb), 0.06);
  border-color: rgba(var(--accent-rgb), 0.35);
}

.ButtonPrimary {
  background-color: var(--accent);
  color: rgba(11, 18, 32, 0.92);
  border-color: var(--accent);
}

.ButtonPrimary:hover,
.ButtonPrimary:focus-visible {
  background-color: var(--accent);
}

.Cards3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 14px;
}

@media (max-width: 920px) {
  .Cards3 {
    grid-template-columns: 1fr;
  }
}

.Grid {
  margin-top: 12px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 14px;
}

.Card {
  grid-column: span 12;
  border: 1px solid var(--border);
  border-radius: 16px;
  padding: 16px;
  background-color: var(--surface);
}

@media (min-width: 760px) {
  .Card {
    grid-column: span 6;
  }
}

.CardTitle {
  margin: 0 0 10px;
  font-size: 16px;
  letter-spacing: 0.2px;
}

.Meta {
  margin: 10px 0 0;
  color: rgba(11, 18, 32, 0.74);
}

.MetaLabel {
  color: var(--accent);
  font-weight: 800;
}

.InlineLink {
  color: var(--accent);
}

.InlineLink:focus-visible {
  outline: none;
  text-decoration: underline;
}

.Footer {
  border-top: 1px solid var(--border);
  padding: 18px 0 10px;
  margin-top: 12px;
}

.FooterInner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  flex-wrap: wrap;
  color: rgba(11, 18, 32, 0.70);
}

.FooterLink {
  color: var(--accent);
  text-decoration: none;
}

.PdfCard {
  display: grid;
  gap: 10px;
}

.PdfFrame {
  width: 100%;
  height: 760px;
  border: 1px solid var(--border);
  border-radius: 14px;
  background: var(--surface);
}

@media (max-width: 920px) {
  .PdfFrame {
    height: 560px;
  }
}

.PdfFallback {
  margin-top: 0;
}

.FooterLink:hover,
.FooterLink:focus-visible {
  outline: none;
  text-decoration: underline;
}
